html {
  background-color: #fbfbf3;
}

.link {
  color: black;
  text-decoration: none;
  font-weight: 600;
  /* font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif; */
  font-size: '200px';
}

.link:hover {
  text-decoration: underline;
  cursor: pointer;
}



.title {
  font-size: 60px;
}

input {
  background-color: transparent;
  width: 250px;
  border-radius: 5px;
  border: 1px solid black;
  height: 30px;
}

button {
  background-color: transparent;
  width: 200%;
  border-radius: 5px;
  border: 1px solid black;
  height: 30px;
  transition: .3s ease;
  margin-left: -50%;
}

button:hover {
  cursor: pointer;
  background-color: rgb(232, 228, 228);
}

.productCover {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.8); /* or darker for contrast */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}

.productCard:hover .productCover {
  opacity: 1;
  cursor: pointer;
}

.productCard:hover {
  cursor: pointer;
}

.navBar {
  position: sticky;
  top: 0;
  z-index: 1000;
}

p {
  text-align: center;
}